"use client";

import { useState } from "react";
import axios from "axios";
import { motion, AnimatePresence } from "framer-motion";

export default function Home() {
  const [phone, setPhone] = useState("");
  const [guest, setGuest] = useState<any>(null);
  const [notFound, setNotFound] = useState(false);
  const [formData, setFormData] = useState({
    first_name: "",
    last_name: "",
    phone: "",
    email: "",
    street_address: "",
    state: "",
    country: "",
    gender: "",
  });

  const handleCheckGuest = async () => {
    try {
      const res = await axios.get(
        `http://127.0.0.1:8000/guest/retrieve/${phone}/`
      );
      setGuest(res.data);
      setNotFound(false);
    } catch (err) {
      setNotFound(true);
    }
  };

  const handleCreateGuest = async () => {
    try {
      await axios.post("http://127.0.0.1:8000/guest/create/", formData);
      alert("Guest registered successfully!");
      setGuest(formData);
      setNotFound(false);
    } catch (err) {
      alert("Error creating guest.");
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-700 to-indigo-900 flex flex-col items-center justify-center p-6 text-white">
      <motion.h1
        initial={{ opacity: 0, y: -50 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-4xl font-bold mb-8"
      >
        Welcome to Our Church üôè
      </motion.h1>

      {/* Phone Input */}
      <div className="w-full max-w-md bg-white/10 backdrop-blur-md rounded-2xl shadow-lg p-6">
        <label className="block text-sm mb-2">Enter Phone Number</label>
        <input
          type="text"
          className="w-full p-3 rounded-xl text-black"
          placeholder="e.g. 08012345678"
          value={phone}
          onChange={(e) => setPhone(e.target.value)}
        />
        <button
          onClick={handleCheckGuest}
          className="mt-4 w-full bg-purple-600 hover:bg-purple-800 py-2 rounded-xl transition"
        >
          Check Guest
        </button>
      </div>

      {/* Show Guest Details if Found */}
      <AnimatePresence>
        {guest && !notFound && (
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0 }}
            className="mt-8 p-6 bg-green-600/80 rounded-xl shadow-lg text-white"
          >
            <h2 className="text-xl font-semibold">Guest Found üéâ</h2>
            <p>
              {guest.first_name} {guest.last_name}
            </p>
            <p>{guest.phone}</p>
            <p>{guest.email}</p>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Show Form if Not Found */}
      <AnimatePresence>
        {notFound && (
          <motion.div
            initial={{ opacity: 0, y: 50 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0 }}
            className="mt-8 w-full max-w-md bg-white/10 backdrop-blur-md p-6 rounded-2xl shadow-lg"
          >
            <h2 className="text-xl font-semibold mb-4">New Guest Form</h2>

            {Object.keys(formData).map((key) => (
              <input
                key={key}
                type="text"
                placeholder={key.replace("_", " ")}
                value={(formData as any)[key]}
                onChange={(e) =>
                  setFormData({ ...formData, [key]: e.target.value })
                }
                className="w-full mb-3 p-3 rounded-xl text-black"
              />
            ))}

            <button
              onClick={handleCreateGuest}
              className="mt-4 w-full bg-green-600 hover:bg-green-800 py-2 rounded-xl transition"
            >
              Register Guest
            </button>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}
